@import "../../App.scss";

.services {
    background: linear-gradient(180deg, #0c0c1d, 111132);
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include mobile {
        transform: none;
    }
}

.textsContainer {
    flex: 1;
    align-self: flex-end;
    display: flex;
    align-items: center;
    gap: 20px;

    @include mobile {
        align-self: center;
        text-align: center;
        flex-direction: column;
    }

    p {
        font-weight: 200;
        font: 20px;
        color: grey;
        text-align: right;
    }

    hr {
        width: 500px;
        border: none;
        border-top: 0.5px solid grey;

        @include mobile {
            width: 300px;
        }
    }

}

.titleContainer {
    flex: 2;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include mobile {
        width: 100%;
    }

    .title {
        display: flex;
        gap: 50px;

        @include mobile {
            flex-direction: column;
            text-align: center;
            gap: 20px;
            align-items: center;
        }

        img {
            width: 300px;
            height: 100px;
            border-radius: 50px;
            object-fit: cover;
            @include mobile {
                    width: 200px;
                    height: 50px;
                }
        }

        h1 {
            font-size: 96px;
            font-weight: 100;

            @include mobile {
                font-size: 36px;
            }
        }

        button {
            width: 300px;
            height: 100px;
            border-radius: 50px;
            background-color: orange;
            border: none;
            font-size: 24px;
            cursor: pointer;

            @include mobile {
                    font-size: 16px;
                    width: 150px;
                    height: 50px;
                }
        }

        button:hover {
            background-color: lightgoldenrodyellow;
            color: rgb(54, 54, 54)
        }
    }
}

.listContainer {
    flex: 2;
    display: flex;
    max-width: 1366px;
    margin: auto;

    @include mobile {
            flex-direction: column;
            width: 100%;
        }

    .box {
        padding: 50px;
        border: 0.5px solid gray;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        @include mobile {
                border: none;
                align-items: center;
                gap: 20px;
                padding: 20px;
            }

        p {
            @include mobile {
                    display: none;
                }
        }
        button {
            padding: 10px;
            background-color: orange;
            border: none;
            cursor: pointer;
            @include mobile {
                    background-color: transparent;
                    border: 1px solid orange;
                    border-radius: 5px;
                    color: orange;
                    width: 50%;
                    padding: 5px;
                }

        }

        button:hover {
            background-color: #0c0c1d;
           
        }
    }

}