@import "../../App.scss";
.contact{
    height: 100%;
    max-width: 1366px;
    margin:auto;
    display: flex;
    align-items: center;
    gap: 50px;

    @include mobile {
        width: 100%;
        flex-direction: column;
        padding: 10px;
    }

    .textContainer{
        flex:1;
        display: flex;
        flex-direction: column;
        gap: 40px;

        @include mobile {
                gap: 20px;
                text-align: center;
                align-items: center;
                margin-top: 70px;
            }

        h1{
            font-size: 100px;
            line-height: 88px;
            @include mobile {
                    font-size: 36px;
                }
        }

        span{
            font-weight: 300;
        }

    }
    .formContainer{
         flex:1;
         position: relative;

        @include mobile {
                padding: 50px;
                width: 100%;
            }
         .phoneSvg {
            position:absolute;
            margin: auto;
            stroke: orange;
            z-index: -1;
            @include mobile {
                    align-items: center;
                }
            svg {

                
                @include mobile {
                        width: 90%;
                        height: 90%;
                        align-items: center;
                    }
            }
         }
         form{

             display: flex;
             flex-direction: column;
             gap: 20px;
    
             input, textarea{
                padding: 20px;
                background-color: transparent;
                border: 1px solid white;
                color: white;
                border-radius: 5px;

                @include mobile {
                        padding:10px;
                    }
             }
             button {
                padding: 20px;
                border: none;
                background-color: orange;
                cursor: pointer;
                font-weight: 500;
                @include mobile {
                        padding: 10px;
                    }
             }
         }
    }
}